<template >
    <div class="activePublic " >
      <!--  -->
      <el-steps :space="200" :active="step" class="step" style="margin:20px 0;">
        <el-step title="" description=""></el-step>
        <el-step title="" description=""></el-step>
        <el-step title="" description=""></el-step>
        <el-step title="" description=""></el-step>
      </el-steps>

        <!--  -->
      <router-view class="view"></router-view>

      <div class="but-group" >
        <el-button ></el-button>
        <el-button ></el-button>
        <el-button  type="primary" @click="next"></el-button>
        <el-button  type="primary"></el-button>
<!--         <router-link :to="{name:'bb'}">bb</router-link>
        <router-link to="/activePublic/aa">Go to Foo</router-link> -->
      </div>

      <!-- <a v-link="a">aa</a> -->
    </div>
</template>

<script>
  export default {
    data() {
      return {
        step: 0
      };
    },

    methods: {
      next() {
        this.step ++;
      }
    }
  }
</script>



